.card-site {
	text-align: center;
	background: #fff;
	max-width: 270px;
	border-radius: 20px;
	box-shadow: 0px 0px 5px 1px rgba(0,0,0,.3);
	padding-bottom: 20px;
	position: relative;
	height: 450px;
	margin: auto;
	margin-top: 0;
	transition: .2s;

	&:hover {
		margin-top: -20px;
		box-shadow: 0px 10px 30px 1px rgba(0,0,0, 1); } }
