.tool {
	text-align: center;
	color: #fff;
	background: rgba(0,0,0,.5);
	border-radius: 20px;
	width: 250px;
	height: 210px;
	transition: .2s;
	padding-top: 20px;
	padding-bottom: 20px;
	position: relative;
	cursor: pointer;
	overflow: hidden;
	&:hover {
		.tool_overlay {
			top: 0; } } }
@media only screen and(max-width: 992px) {
	.tool {} }
